import React from 'react'
import Coffee from './coffee.jpg'
import LondonBus from './LondonBus.JPG'
import Turing from './turing.jpg'
import Container from 'react-bootstrap/Container'
import Row from 'react-bootstrap/Row'
import './Photos.css'

const Photos = () => (
    <div>
        <Container className='PhotographyDiv'>
        	<Row>
	        	<img className='photography' src={Coffee} alt='Coffee'/>
	        	<img className='photographyVert' src={Turing} alt='Turing Machine'/>
	        	<img className='photography' src={LondonBus} alt='London Bus'/>
	        </Row>
        </Container>
    </div>
)
export default Photos